/* ‰∏ªÂÆπÂô® */
.guestbook-container {
  max-width: 600px;
  margin: 20px auto;
  padding: 20px;
  border-radius: 10px;
  background: #f9f9f9;
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
}

/* Ê†áÈ¢ò */
h2 {
  text-align: center;
  color: #333;
}

/* ÈîôËØØÊ∂àÊÅØ */
.error-message {
  color: red;
  text-align: center;
  font-size: 14px;
}

/* Ë°®Âçï */
.guestbook-form {
  display: flex;
  flex-direction: column;
  gap: 10px;
  margin-bottom: 20px;
}

/* ÊñáÊú¨Ê°Ü */
.guestbook-form textarea {
  width: 100%;
  height: 80px;
  padding: 10px;
  font-size: 14px;
  border-radius: 5px;
  border: 1px solid #ccc;
  resize: none;
}

/* ÊåâÈíÆÂíåÈÄâÊã©Ê°Ü */
.form-controls {
  display: flex;
  justify-content: space-between;
}

.form-controls select,
.form-controls button {
  padding: 8px 12px;
  font-size: 14px;
  border-radius: 5px;
  cursor: pointer;
}

.form-controls select {
  border: 1px solid #ccc;
  background: white;
}

.form-controls button {
  background: #4fa050;
  color: white;
  border: none;
}

.form-controls button:disabled {
  background: #aaa;
}

/* ÁïôË®ÄÂå∫ */
.messages {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

/* ÂçïÊù°ÁïôË®Ä */
.message {
  padding: 10px;
  border-radius: 10px;
  color: black; /* ÁïôË®ÄÊñáÊú¨È¢úËâ≤‰∏∫ÈªëËâ≤ */
  max-width: 80%;
  word-wrap: break-word;
}

/* Ë•øË•øÔºàyouÔºâÁöÑÁïôË®ÄÊ†∑Âºè */
.message.you {
  background-color: #ffcccc; /* ÊµÖÁ≤âËâ≤ */
  align-self: flex-start;
  text-align: left;
}

/* ÂçúÂçúÔºàmeÔºâÁöÑÁïôË®ÄÊ†∑Âºè */
